___CSS_LOADER_EXPORT___.push([module.id, `.top-center-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.search-form {
  margin-top: 4vw;
  display: flex;
  flex-direction: column;
  width: min-content;
  gap: 5px;
  font-size: 2rem;
  color: var(--text);
}

.search-box {
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 5px;
  border-bottom: 2px solid white;
}

.search-box > input {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  color: inherit;
  font-size: inherit;
  margin-bottom: 3px;
  width: clamp(100px, 25vw, 300px);
}

.search-box > input:focus {
  border: none;
  outline: none;
}

.search-box > button {
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  height: fit-content;
  padding: 5px;
  margin-bottom: 3px;
}

.search-box > button:hover {
  cursor: pointer;
  background-color: var(--hover-bg);
}

.error-text {
  background-color: var(--error-bg);
  color: var(--text);
  border-radius: 5px;
}

.display-none {
  display: none;
}

.checkbox-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 20px;
}

.radios-container {
  display: flex;
  gap: 20px;
}

/* Customize the label (the container) */
.container {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  user-select: none;
}

/* Hide the browser's default radio button */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: #2196f3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}
